


























































import { Component, Vue } from "vue-property-decorator";

@Component({
  components: {},
})
export default class App extends Vue {
  target = 0;
  width = 0;
  height = 0;
  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  data1!: any;
  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  data2!: any;

  private setBackground(color: string) {
    const back = document.getElementById("background") as HTMLDivElement;
    back.style.backgroundColor = color;
  }
  /**
   * ファイルダイアログを開く
   */
  private opneFile(target: number) {
    this.target = target;
    const input = document.createElement("input");
    input.type = "file";
    input.multiple = true;
    input.addEventListener("change", this.addFile, false);
    input.click();
  }

  /**
   * ファイル追加
   * @param {Event} event  Event
   */
  private addFile(event: Event) {
    const target = event.target as HTMLInputElement;
    const files = target.files as FileList;

    for (const file of files) {
      console.log(file);

      // 画像ファイルチェック
      if (file.type.match("image.*")) {
        this.addImageFile(file);
      }
    }
  }

  /**
   * 画像追加
   * @param {File} file  File
   */
  private addImageFile(file: File) {
    const canvas = document.createElement("canvas");
    const ctx = canvas.getContext("2d");
    if (!ctx) {
      alert("CanvasRenderingContext2D unsupported.");
      return;
    }

    // ファイル読込
    const reader = new FileReader();
    reader.onload = () => {
      // 読込
      const result = reader.result;
      if (!result) {
        alert("FileReader failed.");
        return;
      }

      // 画像表示
      const image = new Image();
      image.src = result as string;
      image.onload = () => {
        // 画像貼付
        this.width = image.width;
        this.height = image.height;
        canvas.width = image.width;
        canvas.height = image.height;
        ctx.clearRect(0, 0, canvas.width, canvas.height);
        ctx.drawImage(image, 0, 0, canvas.width, canvas.height);

        const imageData = ctx.getImageData(0, 0, canvas.width, canvas.height);
        //const pixel = imageData.data;

        if (this.target === 1) {
          this.data1 = imageData;
        } else {
          this.data2 = imageData;
        }
      };
    };
    reader.readAsDataURL(file);
  }

  /**
   * 作成
   */
  private create(isGrayscale: boolean, isExclude: boolean) {
    // Canvas取得
    const canvas = document.getElementById("canvas") as HTMLCanvasElement; //document.createElement("canvas");
    const ctx = canvas.getContext("2d");
    if (!ctx) {
      alert("CanvasRenderingContext2D unsupported.");
      return;
    }
    canvas.width = this.width;
    canvas.height = this.height;

    const pixel1 = (this.data1 as ImageData).data;
    const pixel2 = (this.data2 as ImageData).data;

    const imageData = ctx.getImageData(0, 0, canvas.width, canvas.height);
    const pixel = imageData.data;

    for (let i = 0, n = pixel.length; i < n; i += 4) {
      if (isExclude) {
        // || pixel1[i + 0] + pixel1[i + 1] + pixel1[i + 2] <= allowRange * 3
        if (pixel1[i + 3] == 0) {
          // pixel2[i + 0] + pixel2[i + 1] + pixel2[i + 2] >= 255 * 3 - allowRange * 3
          if (pixel2[i + 3] == 0) {
            // 透過部分は背景化
            pixel[i + 0] = 255; // 赤
            pixel[i + 1] = 255; // 緑
            pixel[i + 2] = 255; // 青
            pixel[i + 3] = 0; // アルファ
          } else {
            const grayscale =
              pixel2[i] * 0.3 + pixel2[i + 1] * 0.59 + pixel2[i + 2] * 0.11;
            pixel[i + 0] = 255; // 赤
            pixel[i + 1] = 255; // 緑
            pixel[i + 2] = 255; // 青
            pixel[i + 3] = grayscale; // アルファ
          }
        } else {
          const grayscale =
            pixel1[i] * 0.3 + pixel1[i + 1] * 0.59 + pixel1[i + 2] * 0.11;
          pixel[i + 0] = 0; // 赤
          pixel[i + 1] = 0; // 緑
          pixel[i + 2] = 0; // 青
          pixel[i + 3] = 255 - grayscale; // アルファ
        }
      } else {
        // || pixel1[i + 0] + pixel1[i + 1] + pixel1[i + 2] <= allowRange * 3
        if (pixel1[i + 3] == 0) {
          // pixel2[i + 0] + pixel2[i + 1] + pixel2[i + 2] <= allowRange * 3
          if (pixel2[i + 3] == 0) {
            // 透過部分は背景化
            pixel[i + 0] = 255; // 赤
            pixel[i + 1] = 255; // 緑
            pixel[i + 2] = 255; // 青
            pixel[i + 3] = 255; // アルファ
          } else {
            const grayscale =
              pixel2[i] * 0.3 + pixel2[i + 1] * 0.59 + pixel2[i + 2] * 0.11;
            pixel[i + 0] = 255; // 赤
            pixel[i + 1] = 255; // 緑
            pixel[i + 2] = 255; // 青
            pixel[i + 3] = grayscale; // アルファ
          }
        } else {
          const grayscale =
            pixel1[i] * 0.3 + pixel1[i + 1] * 0.59 + pixel1[i + 2] * 0.11;
          pixel[i + 0] = isGrayscale ? grayscale : pixel1[i + 0]; // 赤
          pixel[i + 1] = isGrayscale ? grayscale : pixel1[i + 1]; // 緑
          pixel[i + 2] = isGrayscale ? grayscale : pixel1[i + 2]; // 青
          pixel[i + 3] = isGrayscale ? 255 : pixel1[i + 3]; // アルファ
        }
      }
    }
    ctx.clearRect(0, 0, canvas.width, canvas.height);
    ctx.putImageData(imageData, 0, 0);
  }

  /**
   * 作成
   */
  private createMonochrome() {
    // Canvas取得
    const canvas = document.getElementById("canvas") as HTMLCanvasElement; //document.createElement("canvas");
    const ctx = canvas.getContext("2d");
    if (!ctx) {
      alert("CanvasRenderingContext2D unsupported.");
      return;
    }
    canvas.width = this.width;
    canvas.height = this.height;

    const pixel1 = (this.data1 as ImageData).data;
    const pixel2 = (this.data2 as ImageData).data;

    const imageData = ctx.getImageData(0, 0, canvas.width, canvas.height);
    const pixel = imageData.data;

    for (let i = 0, n = pixel.length; i < n; i += 4) {
      // || pixel1[i + 0] + pixel1[i + 1] + pixel1[i + 2] <= allowRange * 3
      if (pixel1[i + 3] == 0) {
        // pixel2[i + 0] + pixel2[i + 1] + pixel2[i + 2] >= 255 * 3 - allowRange * 3
        if (pixel2[i + 3] == 0) {
          // 透過部分は背景化
          pixel[i + 0] = 255; // 赤
          pixel[i + 1] = 255; // 緑
          pixel[i + 2] = 255; // 青
          pixel[i + 3] = 0; // アルファ
        } else {
          pixel[i + 0] = 255; // 赤
          pixel[i + 1] = 255; // 緑
          pixel[i + 2] = 255; // 青
          pixel[i + 3] = 255; // アルファ
        }
      } else {
        pixel[i + 0] = 0; // 赤
        pixel[i + 1] = 0; // 緑
        pixel[i + 2] = 0; // 青
        pixel[i + 3] = 255; // アルファ
      }
    }
    ctx.clearRect(0, 0, canvas.width, canvas.height);
    ctx.putImageData(imageData, 0, 0);
  }

  /**
   * ダウンロード
   */
  private download() {
    const canvas = document.getElementById("canvas") as HTMLCanvasElement; //document.createElement("canvas");

    // BlobオブジェクトにアクセスできるURLを生成
    const base64 = canvas.toDataURL("image/png");
    // Base64からバイナリへ変換
    const bin = atob(base64.replace(/^.*,/, ""));
    const buffer = new Uint8Array(bin.length);
    for (let i = 0; i < bin.length; i++) {
      buffer[i] = bin.charCodeAt(i);
    }

    // Blobを作成
    const blob = new Blob([buffer.buffer], {
      type: "image/png",
    });

    const link = document.createElement("a");
    link.href = window.URL.createObjectURL(blob);
    link.download = "monotrick.png";
    link.target = "_blank";
    link.click();
  }
}
